import {TreeNode} from 'primeng/api';

export interface TreeTexts {
  filterPlaceholder?: string;
  filterBtnClearText?: string;
  filterNotFound?: string;
}

export interface AdaptTreeNode extends TreeNode {
  disallowMultipleNodeSelection?: boolean;
  isGroup?: boolean;
}

export interface AdaptTreeNodeTyped<T> extends AdaptTreeNode {
  data: T;
  children?: AdaptTreeNodeTyped<T>[];
  parent?: AdaptTreeNodeTyped<T>;
}

export interface FilterType {
  filter: string;
  filteredValue: (TreeNode | AdaptTreeNode)[];
}

export interface AdaptDragDropEventType {
  originalEvent: Event;
  dragNode: TreeNode | AdaptTreeNode;
  dropNode: TreeNode | AdaptTreeNode;
  index: number;
  accept?: () => void;
}

export enum TreeWrap {
  Default = 1,
  Wrap = 2,
  WrapAll = 3
}

export enum TreeTrim {
  Default = 1,
  Trim = 2
}
